jQuery(document).ready(function ($) {

    // ---------------------------------------------------------
    // 
    // ---------------------------------------------------------
    $('.menu').dropit();

    // ---------------------------------------------------------
    // 
    // ---------------------------------------------------------
    $(".ui-hover li").each(function () {
        $(this).hover(function () {
            $(this).find(".hide").fadeIn('slow');
        }, function () {
            $(this).find(".hide").fadeOut('fast');
        });
    });


    // ---------------------------------------------------------
    // Tab 
    // ---------------------------------------------------------
    $('.ui-tabs').each(function () {
        var el = $(this);
        el.find('.tab-content-pane:first').show();
    });

    $('.tab-title').each(function () {
        var el = $(this);
        el.find('.tab-title-item:first').addClass('active');
    });

    $('.ui-tabs .tab-title .tab-title-item a').hover(function () {
        var el = $(this),
            parent = el.closest('.ui-tabs'),
            activetab = el.attr('href');
        parent.find('.tab-title-item').removeClass('active');

        el.closest('li').addClass('active');
        parent.find('.tab-content-pane').hide();

        parent.find(activetab).show();

        return false;
    });


    // ---------------------------------------------------------
    // accordion
    // ---------------------------------------------------------
    var all = $('.accordion > dd');
    all.hide(); //

    $('.accordion > dt > a').click(function () { //

        if ($(this).attr('href') === "#") {
            $(this).click(function () {
                return false;
            });
        }

        var next = $(this).parent().next(); //
        if (next.is(":visible")) { //
            all.slideUp('fast');
        } else {
            all.slideUp(); //
            next.slideDown('fast'); //
        }

        return false;
    });


    // ---------------------------------------------------------
    // Toggle
    // ---------------------------------------------------------
    var allt = $('.toggle > dd');
    allt.hide(); //

    $('.toggle > dt > a').click(function () { //

        if ($(this).attr('href') === "#") {
            $(this).click(function () {
                return false;
            });
        }

        var next = $(this).parent().next(); //
        if (next.is(":visible")) { //
            next.slideUp('fast');
        } else {
            next.slideDown('fast'); //
        }

        return false;
    });

});
